import React, { useRef, VFC } from 'react';
import { css } from '@emotion/css';
import { transitionState } from '../modules/store';
import { Background } from './Background';
import { LinkIconButton } from './LinkIconButton';
import { TCanvas } from './three/TCanvas';

export const App: VFC = () => {
	const containerRef = useRef<HTMLDivElement>(null)

	const handleWheel = (e: React.WheelEvent<HTMLDivElement>) => {
		if (transitionState.enabledHandler) {
			if (0 < e.deltaY) {
				// next
				transitionState.prevIndex = transitionState.textureIndex
				transitionState.textureIndex = transitionState.calcNextIndex()
				transitionState.enabledAnimation = true
				transitionState.enabledHandler = false
			} else if (e.deltaY < 0) {
				// prev
				transitionState.prevIndex = transitionState.textureIndex
				transitionState.textureIndex = transitionState.calcPrevIndex()
				transitionState.enabledAnimation = true
				transitionState.enabledHandler = false
			}
		}
	}

	const handleMouseMove = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
		containerRef.current!.style.setProperty('--x', e.clientX + 'px')
	}

	return (
		<div ref={containerRef} className={styles.container} onWheel={handleWheel} onMouseMove={handleMouseMove}>
			<Background />
			<TCanvas />
			<LinkIconButton imagePath="/assets/icons/github.svg" linkPath="https://github.com/nemutas/r3f-unshift-effect" />
		</div>
	)
}

const styles = {
	container: css`
		position: relative;
		width: 100vw;
		height: 100vh;
	`
}
